import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'banner',
	data() {
		return {
			address:  this.Static+'home/flowInlet/address.png'
		};
	},
	props: {
		msgDatas: {
			type: Object
		}
	},
	computed: {
		i18n() {
			return this.$t('listModules');
		},
		...mapGetters([''])
	},
	created() {},
	mounted() {},
	methods: {
		...mapMutations({})
	}
};
